    cursor: pointer;
    i {
      width: 35px;
      height: 35px;
      display: inline-block;
      background: url("../../assets/icons/pwd.png") no-repeat center;
      background-size: 100% 100%;
    }
  }
  .container {
    margin: 0;
    padding: 0;
    background-color: transparent;
    width: 100%;
    height: 200px;
    z-index: 100;
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .div-icon {
    width: 80px;
    height: 40px;
    position: absolute;
    bottom: 90px;
    left: 70px;
    z-index: 999;
    background: url("../../assets/icons/boat.png") no-repeat center;
    background-size: 100% 100%;
    cursor: pointer;
    .font-div {
      position: absolute;
      left: 30px;
      bottom: 8px;
      color: #18aefa;
      font-size: 12px;
    }
    .qz-div {
      width: 85px;
      height: 45px;
      background: url("../../assets/icons/qz.png") no-repeat center;
      background-size: 100% 100%;
      position: absolute;
      color: #fff;
      left: 20px;
      top: -45px;
      z-index: 999;
      padding: 5px 0 0 7px;
      text-align: center;
      font-size: 16px;
      transition: 1s;
      aspect-ratio: 1;
      transform: perspective(400px) rotate3d(var(--i, 1, -1), 0, var(--a));
      mask: linear-gradient(135deg, #000c 40%, #000, #000c 60%) 100% 100%/240% 240%;
      transition: 1s;
    }
    &:hover {
      .qz-div {
        --i: -1, 1;
        mask-position: 0 0;
        transform: scale(1.2);
        // opacity:1.2;
        //  transform: skewx(-25deg);
        // background-image: -webkit-linear-gradient(0deg, rgba(255,255,255,0), rgba(255,255,255,0.5), rgba(255,255,255,0));
      }
    }
  }

  .div-icon2 {
    position: absolute;
    bottom: 90px;
    left: 170px;
  }
  .dy-div2 {
    width: 977px;
    height: 300px;
    position: absolute;
    bottom: 0px;
    left: 10px;
    background: url("../../assets/images/dark.png") no-repeat;
    background-size: 100% 100%;
    pointer-events: none;
  }
  .theme-bk{
    background: url("../../assets/images/light.png") no-repeat!important;
    background-size: 100% 100%!important;
  }
  .dy-div {
    width: 150px;
    height: 100px;
    position: absolute;
    bottom: 140px;
    left: 290px;
    background: url("../../assets/images/true.png") no-repeat;
    background-size: 100% 100%;
    cursor:pointer;
    // z-index:9999;
    color:#111;
    text-align:center;
    line-height: 70px;
    font-size: 22px;
    // font-weight: bold;
    font-family: "AlibabaPuHuiTi";
    // font-family: 'MyFont'

  }
  .right-div{
    position: absolute;
    bottom: 140px;
    left: 490px;
  }
  .dy-div:hover{
    transform: scale(1.2); /*  1.2  */
  }
}
</style>
